.css_folder_manager_field .css_folder_manager_body .css_folder_manager_folder_list .css_folder_manager_folder_item {
  position: relative;
  width: 100%;
  height: 55px;
}

.css_folder_manager_field .css_folder_manager_body .css_folder_manager_folder_list .css_folder_manager_folder_item .css_folder_manager_folder_item_icon {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 55px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.css_folder_manager_field .css_folder_manager_body .css_folder_manager_folder_list .css_folder_manager_folder_item .css_folder_manager_folder_item_icon span.css_material_symbols_rounded {
  width: 20px;
  height: 20px;
  color: var(--b-cssvar-333333);
}

.css_folder_manager_field .css_folder_manager_body .css_folder_manager_folder_list .css_folder_manager_folder_item .css_folder_manager_folder_item_name {
  position: absolute;
  top: 0px;
  left: 55px;
  width: calc(100% - 55px - 55px - 80px);
  height: 55px;
  color: var(--b-cssvar-333333);
  font-size: 20px;
  font-weight: 400;
  line-height: 55px;
  overflow: clip;
  text-overflow: ellipsis;
}

.css_folder_manager_field .css_folder_manager_body .css_folder_manager_folder_list .css_folder_manager_folder_item .css_folder_manager_folder_item_status {
  position: absolute;
  top: 0px;
  right: 45px;
  width: 80px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: right;
  color: var(--b-cssvar-333333-60-flattened);
  white-space: nowrap;
  word-break: keep-all;
}

.css_folder_manager_field .css_folder_manager_body .css_folder_manager_folder_list .css_folder_manager_folder_item .css_folder_manager_folder_item_arrow {
  position: absolute;
  top: 0px;
  right: 17.5px;
  width: 20px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.css_folder_manager_field .css_folder_manager_body .css_folder_manager_folder_list .css_folder_manager_folder_item .css_folder_manager_folder_item_arrow span.css_material_symbols_rounded {
  width: 15px;
  height: 15px;
  font-size: 18px;
  color: var(--b-cssvar-333333-60-flattened);
}